#notfound {
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top:-70px;
}
#notfound .notfound {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.notfound {
    max-width: 920px;
    width: 100%;
    line-height: 1.4;
    text-align: center;
    padding-left: 15px;
    padding-right: 15px;
}
.notfound .notfound-404 {
    height: 100px;
    z-index: -1;
}
.notfound .notfound-404 h1 {
    font-family: maven pro, sans-serif;
    color: #ececec;
    font-weight: 900;
    font-size: 276px;
    margin: 0;
    -webkit-transform: translate(0 -15%);
    -ms-transform: translate(0, -15%);
    transform: translate(0, -15%);
}
.notfound h2 {
    font-family: maven pro, sans-serif;
    font-size: 46px;
    color: #000;
    font-weight: 900;
    text-transform: uppercase;
    margin: 0;
}
.notfound p {
    font-family: maven pro, sans-serif;
    font-size: 16px;
    color: #000;
    font-weight: 400;
    text-transform: uppercase;
    margin-top: 15px;
}
.notfound a {
    font-family: maven pro, sans-serif;
    font-size: 14px;
    text-decoration: none;
    text-transform: uppercase;
    background: #189cf0;
    display: inline-block;
    padding: 16px 38px;
    border: 2px solid transparent;
    border-radius: 40px;
    color: #fff;
    font-weight: 400;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
    width: 250px;
    align-self: center;
    margin-top: 20px;
}
.notfound a:hover {
    background-color: #fff;
    border-color: #189cf0;
    color: #189cf0;
}
@media only screen and (max-width: 480px) {
    .notfound .notfound-404 h1 {
        font-size: 162px;
    }
    .notfound h2 {
        font-size: 26px;
    }
}